export default function (entity) {

    const entityForces = entity.get('forces');

    const spriteSheets = entity.get('sprites');

    delete entityForces.parts.moveLeft;

    delete entityForces.parts.moveRight;

    delete entityForces.parts.escapeRight;

    delete entityForces.parts.escapeLeft;

    spriteSheets.parts.main.animation.frame=0;


    this.scripts.snippets['aJam']['setInputsPerso'].call(this, entity);

    if( spriteSheets.parts.main.source==='perso-walk-right'){
      spriteSheets.parts.main.source='perso-idle-left';
    }else if( spriteSheets.parts.main.source==='perso-walk-left'){
      spriteSheets.parts.main.source='perso-idle-right'
    }
}
